.login{
    background-image: url('/assets/loginback.jpeg');
    background-size:cover;
    width: 100%;
    height: 100%;
    min-height: 94vh;
}


.contLogin{
    border-radius: 20px;
    background:rgb(220, 220, 220); 
    min-height: 74vh;
}

.userImg{
    
    width: 200px;
     height: 180px;
     border-radius: 50%;
     
}

.userImg img{
    width: 100%;
}

.logoFooter img{
    width: 55px;
    margin-bottom: 1%;
}

.GenFooter{
    width: 100%;
    height: 100%;
}

@media screen and (max-width:453px) {
    .logoFooter img{
        width: 50px;
        margin-bottom: 5%;
    }
}
.lateralMenu{
    background:rgb(1, 35, 57);
